<script lang="babel">
  import Clipboard from "clipboard"
  export default{
    name: 'deal',
    data(){
      return {
        query:{
          page:1,
          size:10,
          orderNo:'',
          merchantName: '',
          startTime: '',
          endTime: '',
          lessTotalFee: '',
          moreTotalFee: '',
          status: '',
          settleStatus:'',
          payType:'',
          proxyName:'',
          proxyName1:''
        },
        date:'',
        records: [],
        count: 0,
        total: 0,
        loading: true,
        url:''
      }
    },
    created: function () {
      var clipboard = new Clipboard('.td');
      //
      clipboard.on('success', (e)=> {
        this.$store.commit('MESSAGE_ACCORD_SHOW', {
          text: "  "+e.text
        })
      });
      this.getData()
    },
    methods: {
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/queryOrder/orderList',this.$data.query)
          .then(function (res) {
            this.loading = false;
            this.$data.records =res.data.records;
            this.$data.total=res.data.totalPage;
            this.$data.url=res.data.ext;
            this.$data.count = res.data.count;
          },function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      //hss
      changeTime: function (row, column) {
        var val=row.createTime;
        if(val==''||val==null){
          return ''
        }else {
          val = new Date(val)
          var year=val.getFullYear();
          var month=val.getMonth()+1;
          var date=val.getDate();
          var hour=val.getHours();
          var minute=val.getMinutes();
          var second=val.getSeconds();
          function tod(a) {
            if(a<10){
              a = "0"+a
            }
            return a;
          }
          return year+"-"+tod(month)+"-"+tod(date)+" "+tod(hour)+":"+tod(minute)+":"+tod(second);
        }
      },
      changeNum: function (row, column) {
        var val = row.tradeAmount;
        return parseFloat(val).toFixed(2);
      },
      changePayType: function (row, column) {
        var val = row.payType;
        if(val == "S"){
          return ""
        }else if(val == "N"){
          return ""
        }else if(val == "H"){
          return "H5"
        }else if(val == "B"){
          return ""
        }else if(val == "Z"){
          return ""
        }
      },
      changeStatus: function (row, column) {
        var val = row.status;
        if(val == 1){
          return ""
        }else if(val == 3){
          return ""
        }else if(val == 4){
          return ""
        }else if(val == 5){
          return ""
        }else if(val == 6){
          return ""
        }else if(val == 7){
          return ""
        }else if(val == 6){
          return ""
        }
      },
      changeSettleStatus: function (row, column) {
        var val = row.settleStatus;
        if(val == 2){
          return ''
        }else if(val == 1){
          return ''
        }else if(val == 3){
          return ''
        }
      },
      changePayChannel: function (row, column) {
        var val = row.payChannelSign;
        if(val == 101){
          return ''
        }else if(val == 102){
          return ''
        }else if(val == 103){
          return ''
        }
      },
      changeHide: function (row, column) {
        var val = row.sn;
        if(val!=""&&val!=null){
          val = val.replace(val.substring(3,val.length-6),"");
        }
        return val
      },
      changeHide1: function (row, column) {
        var val = row.orderNo;
        if(val!=""&&val!=null){
          val = val.replace(val.substring(3,val.length-6),"");
        }
        return val
      },
      search(){
        this.$data.query.page = 1;
        this.getData()
      },
      //
      handleSizeChange(val) {
        this.$data.query.page = 1;
        this.$data.query.size = val;
        this.getData()
      },
      //
      handleCurrentChange(val) {
        this.$data.query.page = val;
        this.getData()
      },
    },
    watch:{
      date:function (val,oldVal) {
        if(val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.startTime = str;
            }else {
              this.$data.query.endTime = str;
            }
          }
        }else {
          this.$data.query.startTime = '';
          this.$data.query.endTime = '';
        }
      }
    },
  }
</script>
<style scoped lang="less" rel="stylesheet/less">
  ul{
    padding: 0;
  }
  .same{
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }
  .price{
    display:inline-block;
    width: 210px;
    height:30px;
    /*line-height: 30px;*/
    border-radius: 4px;
    border-color: #bfcbd9;
    position: relative;
    top: 6px;
    input{
      border: none;
      display:inline-block;
      width: 45%;
      height: 25px;
      position: relative;
      top: -3px;
    }
  }
  .price:hover {
    border-color: #20a0ff;
  }

</style>




